import { ResponseError, ResponseSuccess } from '@/types/api'
import axios, { AxiosInstance, AxiosError, AxiosResponse, AxiosRequestConfig } from 'axios'

const responseError: ResponseError[] = [
    {
        status: 400,
        message: ' (400)',
    },
    {
        status: 401,
        message: ' (401)',
    },
    {
        status: 403,
        message: ' (403)',
    },
    {
        status: 404,
        message: ' (404)',
    },
    {
        status: 408,
        message: ' (408)',
    },
    {
        status: 500,
        message: ' (500)',
    },
    {
        status: 501,
        message: ' (501)',
    },
    {
        status: 502,
        message: ' (502)',
    },
    {
        status: 503,
        message: ' (503)',
    },
    {
        status: 504,
        message: ' (504)',
    },
    {
        status: 505,
        message: 'HTTP (505)',
    },
]

const instance: AxiosInstance = axios.create({
    baseURL: process.env.VUE_APP_BASE_URL,
    // withCredentials: true,
    timeout: 10000,
})

instance.interceptors.request.use((config: AxiosRequestConfig): AxiosRequestConfig | Promise<AxiosRequestConfig> => {
    if (config.method === 'POST') {
        if (!config.headers) { config.headers = {} }
        config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
    }
    return config
})

instance.interceptors.response.use((response: AxiosResponse): Promise<AxiosResponse<ResponseSuccess>> => {
    return response.status === 200 ? Promise.resolve(response.data) : Promise.reject(response)
}, (error: AxiosError) => {
    if (error && error.response) {
        const itemError: ResponseError | undefined = responseError.find((item) => item.status === (error.response as AxiosResponse).status)
        error.message = itemError ? itemError.message : `(${error.response.status})!`
    } else {
        error.message = ''
    }
    return Promise.reject(error)
})


export default instance
